* {
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}

body {
  font-family: 'Noto Sans UI', Arial, sans-serif;
  font-size: $m-size;
}

button {
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

.is-active {
  font-weight: bold;
}
.img-width {
  width: 100px;
  height: 100px;
}



